#reactContainer {
	width: 100%;
	height: 100vh;
}

#main {
	width: 100vw;
	min-height: calc(100vh - 6rem);
	margin: 3rem auto 0 auto;

	display: flex;
	flex-direction: column;
	justify-items: flex-end;
	align-content: flex-end;
}

#Header {
	h1 {
		font-size: 2.3rem;
		font-weight: 300;
		text-align: center;
		margin-bottom: 1rem;
	}
}

#MeasurementContainer {
	h2 {
		font-size: 1.4rem;
		margin-bottom: 0.85rem;
	}

	button {
		font-family: 'Roboto', sans-serif;
		font-weight: 300;
		font-size: 0.8rem;
		padding: 0.3rem 1.6rem 0.3rem 1.6rem;
		border: 1px solid rgb(99, 99, 99);
		border-radius: 3px;
		background-color: $color-bg;
		color: rgb(99, 99, 99);
		cursor: pointer;

		transition: all 300ms ease-in-out;

		&:hover {
			background-color: rgb(99, 99, 99);
			color: rgb(226, 226, 226);
		}
	}

	hr {
		background-color: rgb(170, 170, 170);
		height: 1px;
		border: none;
		margin-bottom: 3rem;

		&.topper {
			margin-top: 1.5rem;
			margin-bottom: 2rem;
		}
	}

	.measurementcontainer {
		&-measurements {
			&__loading {
				font-size: 2.2rem;
			}
		}

		&-controls {
			min-width: 30rem;
			width: 30%;
			margin: 0 auto;

			&-group {
				margin: 1rem 1rem 1rem 1rem;
				font-size: 1rem;

				select {
					width: 100%;
				}

				&-group-time {
					&__header {
						p {
							display: block;
							width: 100%;
						}

						label {
							display: inline-block;
							width: 40%;
						}

						select {
							display: inline-block;
							width: 55%;
						}
					}
				}

				&__calculation-header {
					display: flex;
					justify-content: space-evenly;

					* {
						align-self: center;
					}
				}

				&-number-calculation-field {
					display: flex;
					justify-content: space-evenly;
					margin-top: 0.5rem;

					&__name {
						width: 65%;
					}

					&__function {
						width: 20%;
					}

					input[type='submit'] {
						width: 10%;
					}
				}

				&-date,
				&-time {
					display: inline-block;

					& label {
						display: block;
					}

					input {
						width: 100%;
						box-sizing: border-box;
					}
				}

				&-date {
					width: 55%;
				}

				&-time {
					width: 40%;
				}
			}
		}
	}

	.measurement {
		background-color: white;
		margin: 0 0 1.2rem 0;
		border: 1px solid #707070;

		&-header {
			text-align: right;
			font-size: 1.1rem;
			font-weight: 300;
			border-bottom: 1px solid black;
			background-color: #d1cece;
			padding: 0.3rem 0 0 0.3rem;
			display: flex;
			justify-content: space-between;

			&-title {
				line-height: 1.5rem;
				font-size: 1.2rem;
			}

			&-actions {
				display: flex;
				height: 100%;
			}
		}

		&-fields {
			display: flex;
			justify-content: space-evenly;
			flex-wrap: wrap;
			* {
				color: #707070;
				font-size: 1rem;
				text-align: center;
				font-weight: 900;
			}

			.measurement-field {
				width: 18.75rem;
				margin: 0.75rem 0;
				padding: 0.5rem 0;
				&-title {
					border-bottom: 1px solid #707070;
					padding-bottom: 0.5rem;
					margin-bottom: 0.5rem;
				}
			}
		}

		&-footer {
			text-align: center;
			font-size: 0.9rem;
			font-weight: 300;
			border: 1px solid black;
		}
	}
	.detailed-measurement {
		background-color: white;
		// margin: 0 0 1.2rem 0;
		// border: 1px solid #707070;
		padding: 1rem;

		&-field {
			display: flex;
			& * {
				text-align: left;
				font-size: 0.9rem;
				border: 1px solid #9b9b9b;
				padding: 1rem;
			}
			&-title {
				width: 20%;
			}
			&-content {
				width: 80%;
			}
		}
	}
}

select,
input {
	padding: 0.5rem;
	border-radius: 0.3rem;
	border: 1px solid #ccc;
}

input[type='submit'] {
	padding: 0.5rem;
	background: none;
	color: black;
	border-radius: 0.3rem;
	border: 1px solid #ccc;

	&:hover {
		background: #ccc;
	}
}

#ChartContainer {
	hr {
		background-color: rgb(170, 170, 170);
		height: 1px;
		border: none;
		margin-bottom: 3rem;

		&.topper {
			margin-top: 1.5rem;
			margin-bottom: 2rem;
		}
	}

	.chartcontainer-controls {
		&-group {
			font-size: 1rem;
			line-height: 1rem;

			* {
				margin-right: 1rem;
				margin-bottom: 0.5rem;
			}
		}
	}
}

.d3-chart {
	&-label {
		font-size: 0.7rem;
	}

	&-legend {
		&__text {
			font-size: 0.7rem;
		}
	}

	&__axis {
		.tick {
			line {
				opacity: 15%;
			}

			text {
				font-size: 0.7rem;
			}
		}
	}
}

#Tooltip {
	display: grid;
	grid-template-columns: auto;

	border: 1px solid #ccc;
	border-radius: 0.5rem;
	background: #fff;

	* {
		font-size: 1rem;
		text-align: center;
		color: black;
		margin: 0;
	}

	.tooltip {
		&__title {
			font-weight: 700;
		}

		&__value {
			margin-bottom: 1rem;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

button {
	// border-radius: 0.7rem;
	// height: 3.4rem;
	// font-size: 1.8rem;
	font-size: 1rem;
	padding: 0.2rem;
	transition: all 0.3 ease-in-out;
}

.button-transparent {
	color: $color-transparent-button-text;
	background: none;
	border: 1px solid $color-secondary;

	&:hover {
		color: $color-opaque-button-text;
		background-color: $color-primary;
		border: 1px solid $color-primary;
	}

	&:active {
		color: $color-transparent-button-text;
		background: none;
		border: 1px solid $color-secondary;
	}
}

.button-opaque {
	color: $color-opaque-button-text;
	background-color: $color-primary;
	border: 1px solid $color-primary;

	&:hover {
		color: $color-transparent-button-text;
		background: none;
		border: 1px solid $color-secondary;
	}

	&:active {
		color: $color-opaque-button-text;
		background-color: $color-primary;
		border: 1px solid $color-primary;
	}
}

#RangedInput {
	padding: 1rem;
	border: 1px solid #ccc;
	border-radius: 0.2rem;

	.rangedinput {
		&-heading {
			display: flex;
			justify-content: space-between;
		}

		&-toggle {
			display: flex;
		}

		&-singular-group,
		&-ranged-group {
			margin-top: 0.5rem;
		}

		&-singular-group {
			display: flex;
			justify-content: space-evenly;
		}

		&-ranged-group {
			display: flex;
			justify-content: space-evenly;

			&__label {
				font-size: 1.7rem;
				font-weight: 100;
			}
		}
	}
}

.toggle {
	direction: ltr;
	width: 1.5rem;
	height: 0.75rem;
	border: 0.1rem solid lightslategray;
	border-radius: 0.1rem;
	background-color: #ccc;
	padding: 0.2rem;
	cursor: pointer;

	&-on {
		direction: rtl !important;
		background-color: salmon !important;
	}

	&__rect {
		height: 100%;
		width: 50%;
		background-color: white;
		align-self: center;
	}
}

.error-border {
	border: 1px solid red;
}

.display-flex-evenly {
	display: flex;
	justify-content: space-evenly;
}
